@import "./../App/constants";

div:hover {
  cursor: default;
}
.basket__order-result {
  background-color: $table-bg;
  padding: 21px 0 16px;
}
.basket__order-result-header {
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  padding: 0 0 18px 22px;
  border-bottom: 1px solid $table-border-color;
}
.basket__order-result-info {
  padding: 29px 18px 21px 16px;
}
.basket__order-promo {
  margin-bottom: 28px;
}
.basket__order-promo-input {
  padding-left: 16px;
  font-size: 13px;
  line-height: 16px;
  min-height: 40px;
  color: $input-font-color;
}
.basket__order-promo-confirm, .basket__order-submit {
  border-radius: 0;
  background-color: $buttons-color;
  border: 1px solid #4CB7EF;
}
.basket__order-promo-confirm {
  min-width: 79px;
}
.basket__order-promo-confirm {
  font-size: 13px;
  line-height: 16px;
}
.basket__order-result-discount-text, .basket__order-result-delivery-text {
  font-size: 12px;
  color: #777;
  line-height: 15px;
}
.basket__order-result-discount-number {
  color: #FF0000;
}
.basket__order-result-discount-number, .basket__order-result-delivery-number {
  font-size: 12px;
}
.basket__order-result-delivery-text {
  margin-bottom: 25px;
}
.basket__order-total-text {
  margin-bottom: 18px;
}
.basket__order-total-number {
  font-size: 16px;
}
.basket__order-submit {
  font-size: 16px;
  line-height: 20px;
  height: 58px;
}
